import {
  APIProvider,
  Map,
  AdvancedMarker,
  Pin,
} from "@vis.gl/react-google-maps";

const CustomMarker = () => {
  return (
    <AdvancedMarker
      key={"beach house"}
      position={{ lat: 35.9208629, lng: -75.6039274 }}
    >
      <Pin background={"#FF3F3F"} glyphColor={"#000"} borderColor={"#000"} />
    </AdvancedMarker>
  );
};

export default function GoogleMap() {
  return (
    <APIProvider
      apiKey="AIzaSyCv2CjpzUlvxTvHgFaRSlkrYxP4RoBG9x0"
      onLoad={() => console.log("Maps API has loaded.")}
    >
      <div className="w-full h-72 rounded-lg border-8 overflow-hidden">
        <Map
          defaultZoom={8}
          defaultCenter={{ lat: 35.9208629, lng: -75.6039274 }}
          mapId="2524d89f865da406c88920cb"
        >
          <CustomMarker />
        </Map>
      </div>
    </APIProvider>
  );
}
